import API  from '@/store/api';
import { getLink } from '@/store/links'

const actions = {
    setDatePickerResetState({ commit }, flag) {
        commit("setDatePickerResetFlagState", flag);
    },
    changeTab({ commit }, tabName) {
        commit("setTab", tabName);
    },
    setFabVisibility({ commit }, fabState) {
        commit("setFabState", fabState);
    },
    changeSearchTab({ commit }, searchTab) {
        commit("setSearchTab", searchTab);
    },
    changeViewOption({ commit }, option) {
        commit("setViewOption", option);
    },

    populateFirstPage( {commit} ){
        const params = {
            query : true,
            queries: {
                size : 12,
            }
        }


        API.get( getLink('tender', params), null ).then( ( tenderData) => {
                commit("setTenderData", tenderData.data)
                commit("setTenderPageInfo", tenderData);
            }
        ).catch( (err) => {
            console.log(err)
        })


        API.get( getLink('award', params), null ).then( ( awardData) => {
                commit("setAwardData", awardData.data)
                commit("setAwardPageInfo", awardData);
            }

        ).catch( (err) => {
            console.log(err)
        })


        API.get( getLink('company', params), null ).then( ( companyData) => {
                commit( "setCompanyData", companyData.data )
                commit( "setCompanyPageInfo", companyData )
            }
        ).catch( (err) => {
            console.log(err)
        })

        if(localStorage.getItem("accessToken") &&  localStorage.getItem("accessToken") != 'null'){
            API.get( getLink('getUser', {}), null ).then( ( user) => {
                    commit( "setUser", user )
                }
            ).catch( (err) => {
                console.log(err)
            }) 
        }
    },

}

export default actions;