<template>
  <v-data-table
    dense
    :headers="getTenderHeader"
    :items="getTenderData"
    :server-items-length="getTenderPageInfo.totalCount"
    :footer-props="footerProps"
    :options="{ itemsPerPage:12 }"
    @update:page="changePage" 
    @update:sort-by="sortData"
    @update:sort-desc="revSortData"
    class="elevation-1 mt-5"
  >
    <template v-slot:item.title="{ item }">
      <v-layout>
        <v-flex xs2>
          <v-icon left class="tender-icons ma-0 pa-0" :color="getColor(item.status)" >
                {{ getIcon( item.status ) }}
          </v-icon>
        </v-flex>
        <v-flex xs10>
          {{ item.title }}
        </v-flex>
      </v-layout>
    </template>
    
    <template v-slot:item.detail="{ item }">
      <TenderDetailCard  :tenderData=item />
    </template>
  </v-data-table>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import TenderDetailCard from '@/components/Tender/TenderDetailCard';

export default {
    components: {
      TenderDetailCard
    },
    data () {
      return {
        footerProps: {'items-per-page-options': []},
      }
    },
    computed: {
        ...mapGetters(["getTenderData","getTenderHeader", "getTenderPageInfo", "getTenderSearchParams"]),
    },
    methods: {
      ...mapActions(["searchTenders", "setTenderData"]),
      getColor (status) {
        if (status == 'Approved') return 'green'
        else if (status == 'Published By User') return 'orange'
        else return 'red'
      },
      getIcon (status) {
        if (status == 'Approved') return 'mdi-check'
        else if (status == 'Published By User') return 'mdi-progress-check'
        else return 'mdi-close'
      },
      changePage(pageNumber){
        let params = this.getTenderSearchParams;
        params.queries.page = pageNumber;
        this.setTenderData([])
        this.searchTenders(params)
      },
      sortData(info){
        this.getTenderData.sort( (a, b) => (a[ info[0] ] > b[ info[0] ] ? 1 : -1) )
      },
      revSortData(info){
        this.getTenderData.sort( (a, b) => (a[ info[0] ] > b[ info[0] ] ? -1 : 1) )
      }
      
    },
  }
</script>

<style scoped>

.tender-card-chip{
  font-size: 10px !important;
  font-weight: 400 !important;
  line-height: 6px !important;
}

.tender-icons{
  font-size: 17px !important;
  font-weight: 400 !important;

}
.v-data-table >>> td{
  font-size: 10px !important;
  font-weight: 400 !important;
}

</style>