<template>
    <v-card :elevation="4" class="ml-2" style="margin-right: 50px !important">
        <v-tabs
          class="font-weight-black "
          v-model="tab"
          background-color="transparent"
          color="basil"
          grow
        >
          <v-tab
            v-for="item in items"
            @change="change(item)"
            :key="item"
            class="display-tab"
          >
            {{ item }}
          </v-tab>
        </v-tabs>

        <v-tabs-items v-model="tab">
          <v-tab-item
            v-for="item in items"
            :key="item"
          >
          <v-container fluid>
            <div v-if="!getViewOption">
              <ListContainer/>
            </div>
            <div v-else>
              <TableContainer/>
            </div>
          </v-container>
 
          </v-tab-item>
        </v-tabs-items>
        </v-card>
</template>


<script>
import ListContainer from '@/components/common/ListContainer';
import TableContainer from '@/components/common/TableContainer';
import { mapGetters } from "vuex";
import { mapActions } from "vuex";

export default {
    components:{
      ListContainer,
      TableContainer,
    },
    data () {
        return { 
            tab: null,
            items: [
            'Tender', 'Company', 'Award',
            ],
        }
    },
    methods: {
      ...mapActions(["changeTab"]),
      
      change(TabName){
        if (TabName != this.tab ){
          this.tab = TabName;
          this.changeTab(TabName);
        }
      },
    },
    computed: {
        ...mapGetters(["getViewOption"]),
    }
    
}
</script>

<style scoped>

.display-tab{
  font-size: 10px !important;
  font-weight: 400 !important;
}

</style>