<template>
    <div>
        <v-card class="" :elevation="4">
            <v-card-title class="font-weight-black vuetify-main-components"> Company Search </v-card-title>
        </v-card>

        <v-card class="mt-2 pa-5" :elevation="4">
            <v-row>
                <v-col xs12 id="company-name">
                    <v-combobox
                        clearable
                        hide-selected
                        deletable-chips
                        multiple
                        persistent-hint
                        dense
                        small-chips
                        v-model="selected_values"
                        :items= "items"
                        height = 100
                        >
                    </v-combobox>
                    <v-card-title class="font-weight-black vuetify-main-components "> Company Informations </v-card-title>
                </v-col>
            </v-row>
        </v-card>
        
        <v-card class="mt-2 pa-5" :elevation="4">
            <v-row class="ma-0 pa-0">
                <v-col id="company-category"  cols="12" sm="6"  class="ma-0 pa-0">
                    <v-autocomplete
                    chips
                    clearable
                    deletable-chips
                    multiple
                    v-model="selected_categories"
                    small-chips
                    dense
                    :items= "getCompanyCategories"
                    >
                    </v-autocomplete>
                    <v-card-title class="font-weight-black vuetify-main-components "> Category </v-card-title>
                </v-col>

                <v-col id="company-country"  cols="12" sm="6"    class="ma-0 pa-0 pl-1 ml-auto">
                    <v-autocomplete
                        chips
                        clearable
                        deletable-chips
                        multiple
                        dense
                        v-model="selected_countries"
                        small-chips
                        :items= "getCompanyCountries"
                        >
                    </v-autocomplete>
                    <v-card-title class="font-weight-black vuetify-main-components"> Country </v-card-title>
                </v-col>
            </v-row>
        </v-card>   

        <v-card class="mt-2 pa-5" :elevation="4">
            <v-row>
                <v-col cols="9">
                        <v-btn
                            color="red"
                            text
                            dense
                            @click="clearResult"
                            large rounded  
                            class="float-right"
                            style="font-size:10px !important"     
                        >
                            Clear
                        </v-btn>
                </v-col>
                <v-col cols="3">
                    <v-btn
                        color="accent"
                        elevation="5"
                        dense
                        @click="searchResult"
                        large outlined raised rounded text
                        class="float-right"     
                        style="font-size:10px !important" 
                    >
                        Search
                    </v-btn>
                </v-col>
            </v-row>
        </v-card>

    </div>
</template>


<script>
import { mapGetters, mapActions } from 'vuex'

export default {
    components: {
    },
    data() {
        return {
            items: [ "Ethiopia", "PPA", "Construction" ],
            selected_countries: [],
            selected_values: [],
            selected_categories:[],
        };
    },
    computed: {
        ...mapGetters(["getCompanyCountries", "getCompanyCategories" ]),
    },
    methods: {
        ...mapActions(["searchCompanies", "setCompanyData", "setCompanySearchParams"]),
        searchResult(){
            let searchQueries = {
                query: true,
                queries:{
                    size: 12,
                    name : this.selected_values.join(','),
                    country : this.selected_countries.join(','),
                    category : this.selected_categories.join(','), 
                }
            }
            this.setCompanyData( [] )
            this.setCompanySearchParams( searchQueries )
            this.searchCompanies( searchQueries )
        },
        clearResult(){
            let searchQueries = {
                query: true,
                queries:{
                    size: 12,
                }
            }
            this.selected_values = []
            this.selected_countries = []
            this.selected_categories = []
            
            this.setCompanyData( [] )
            this.setCompanySearchParams( searchQueries )
            this.searchCompanies( searchQueries )
        }
    }
}
</script>