<template>
  <v-data-table
    dense
    :headers="getCompanyHeader"
    :items="getCompanyData"
    :server-items-length="getCompanyPageInfo.totalCount"
    :footer-props="footerProps"
    :options="{ itemsPerPage:12 }"
    @update:page="changePage" 
    @update:sort-by="sortData"
    @update:sort-desc="revSortData"
    class="elevation-1 mt-5"
  > 
    <template v-slot:item.detail="{ item }">
      <CompanyDetailCard  :companyData=item />
    </template>
  </v-data-table>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import CompanyDetailCard from '@/components/Company/CompanyDetailCard';

export default {
    components: {
      CompanyDetailCard
    },
    data () {
      return {
        footerProps: {'items-per-page-options': []},
      }
    },
    computed: {
        ...mapGetters(["getCompanyData","getCompanyHeader", "getCompanyPageInfo" , "getCompanySearchParams"]),
    },
    methods: {
      ...mapActions(["searchCompanies", "setCompanyData"]),
      getColor (status) {
        if (status == 'Approved') return 'green'
        else if (status == 'Published By User') return 'orange'
        else return 'red'
      },
      getIcon (status) {
        if (status == 'Approved') return 'mdi-check'
        else if (status == 'Published By User') return 'mdi-progress-check'
        else return 'mdi-close'
      },
      changePage(pageNumber){
        let params = this.getCompanySearchParams;
        params.queries.page = pageNumber;
        this.setCompanyData([])
        this.searchCompanies(params)
      },
      sortData(info){
        this.getCompanyData.sort( (a, b) => (a[ info[0] ] > b[ info[0] ] ? 1 : -1) )
      },
      revSortData(info){
        this.getCompanyData.sort( (a, b) => (a[ info[0] ] > b[ info[0] ] ? -1 : 1) )
      }
    },
  }
</script>

