<template>
    <div class="mr-0">
        <v-navigation-drawer @transitionend="updateState" width="70px" :value="getSideBarState" bottom dark app>
            <v-list nav>
                <Profile/>
                <v-divider></v-divider>

                <v-list dense >
                    <v-list-item
                        v-for="item in items"
                        :key="item.title"
                        link
                    >
                        <v-tooltip bottom>
                            <template v-slot:activator="{ on, attrs }">
                                <v-list-item-icon v-bind="attrs" v-on="on" :style=" $vuetify.breakpoint.mdAndDown? '' : 'position:absolute; left:0%' " @click=" excuteRequest( item.title ) ">
                                    <v-icon>{{ item.icon }}</v-icon>
                                </v-list-item-icon>
            
                                <v-list-item-content v-if="! $vuetify.breakpoint.lgAndUp" @click=" excuteRequest( item.title ) ">
                                    <v-list-item-title>{{ item.title }}</v-list-item-title>
                                </v-list-item-content>
                    
                            </template>
                            <span>{{ item.title }}</span>
                        </v-tooltip>
                    </v-list-item>
                </v-list>
            </v-list>
        </v-navigation-drawer>
    </div>
</template>

<script>
import {  mapActions, mapGetters } from "vuex";
import Profile from "@/components/User/Profile"

export default {
    components: {
        Profile,
    },
    data: () => ({
        drawer: true,
        items: [
            { title: 'Home', icon: 'mdi-home-city' },
            { title: 'My Account', icon: 'mdi-account' },
            { title: 'Users', icon: 'mdi-account-group-outline' },
            { title: 'Logout', icon: 'mdi-logout' },
        ],
        mini: true,

    }),
    computed: {
         ...mapGetters(["getSideBarState", "getSideBarBurgerState", "getSideBarBurgerState"]),
    },
    methods: {
        ...mapActions(['logoutUser', 'setSideBarState', 'setSideBarBurgerState', "setUserValue", "setLoggedInStateValue"]),
        excuteRequest( req ){
            console.log('logged out!')
            if( req == 'Logout' ){
                this.logoutUser();
                this.setLoggedInStateValue();
                this.setUserValue();
            }
        },
        updateState( state ){
            if(state.propertyName == 'visibility'){
                this.setSideBarState( !this.getSideBarBurgerState );
                this.setSideBarBurgerState( !this.getSideBarBurgerState);
                // console.log(this.getSideBarBurgerState, this.getSideBarState)
            }
        },
    }
}
</script>

<style scoped>
.v-overlay {
  z-index: 6 !important;
}
</style>