<template>
    <div>
        <v-card class="" :elevation="4">
            <v-card-title class="font-weight-black vuetify-main-components"> Tender Search </v-card-title>
        </v-card>

        <v-card class="mt-2 pa-5" :elevation="4">
            <v-row>
                <v-col xs12 id="tender-name">
                    <v-combobox
                        class="combo-box"
                        clearable
                        hide-selected
                        deletable-chips
                        multiple
                        persistent-hint
                        dense
                        full-width
                        small-chips
                        :items= "items"
                        v-model="selected_values"
                        height= 80
                        >
                    </v-combobox>
                    <v-card-title class="font-weight-black vuetify-main-components ma-0" > Tender Informations </v-card-title>
                </v-col>
            </v-row>
        </v-card>
        
        <v-card class="mt-2 pa-5" :elevation="4">
            <v-container fluid class="ma-0 pa-0">
                <v-row class="ma-0 pa-0">
                    <v-col id="tender-sector" cols="12" sm="6"  class="ma-0 pa-0 pr-1">
                        <v-autocomplete
                            class="combo-box"
                            chips
                            clearable
                            deletable-chips
                            multiple
                            dense
                            small-chips
                            v-model="selected_sectors"
                            :items= "getTenderSector"
                        >
                        </v-autocomplete>
                        <v-card-title class="font-weight-black vuetify-main-components"> Sector </v-card-title>
                    </v-col>

                    <v-col id="tender-type" cols="12" sm="6" class="ma-0 pa-0" >
                        <v-autocomplete
                            chips
                            clearable
                            deletable-chips
                            multiple
                            dense
                            small-chips
                            v-model="selected_types"
                            :items= "getTenderType"
                            >
                        </v-autocomplete>
                        <v-card-title class="font-weight-black vuetify-main-components"> Type </v-card-title>
                    </v-col>
                </v-row>
            </v-container>
        </v-card>   

        <v-card class="mt-2 pa-5" :elevation="4">
            <v-container fluid class="ma-0 pa-0">
                <v-row class="ma-0 pa-0">
                    <v-col id="tender-company"  cols="12" sm="6"  class="ma-0 pa-0 pr-1">
                        <v-autocomplete
                            chips
                            clearable
                            deletable-chips
                            multiple
                            dense
                            small-chips
                            v-model="selected_companies"
                            :items= "getCompanyNames"
                            >
                        </v-autocomplete>
                        <v-card-title class="font-weight-black vuetify-main-components"> Company </v-card-title>
                    </v-col>

                    <v-col id="tender-country"  cols="12" sm="6" class="ma-0 pa-0" >
                        <v-autocomplete
                            chips
                            clearable
                            deletable-chips
                            multiple
                            dense
                            small-chips
                            v-model="selected_countries"
                            :items= "getTenderCountries"
                            >
                        </v-autocomplete>
                        <v-card-title class="font-weight-black vuetify-main-components"> Country </v-card-title>
                    </v-col>
                </v-row>
            </v-container>
        </v-card>

        <v-card class="mt-2 pa-5" :elevation="4">
            <v-container fluid>
                <v-row  id="tender-issued" >
                    <v-col  cols="12" sm="6"  class="ma-0 pa-0 pr-1">
                        <DatePicker label = "Issued starting date" @date="updateIssuedDateStart" /> 
                    </v-col>

                    <v-col  cols="12" sm="6" class="ma-0 pa-0">
                        <DatePicker label = "Issued ending date" @date="updateIssuedDateEnd" /> 
                    </v-col>
                </v-row>
            </v-container>
            <v-card-title class="font-weight-black vuetify-main-components"> Isssued Date </v-card-title>
        </v-card>

        <v-card class="mt-2 pa-5" :elevation="4">
            <v-container fluid class="ma-0 pa-0">
                <v-row  id="tender-closing" class="ma-0 pa-0 mt-2" >
                    <v-col  cols="12" sm="6"  class="ma-0 pa-0 pr-1">
                        <DatePicker label = "Closing starting date"  @date="updateClosingDateStart" /> 
                    </v-col>

                    <v-col  cols="12" sm="6"  class="ma-0 pa-0 ml-auto">
                        <DatePicker label = "Closing ending date" @date="updateClosingDateEnd"  /> 
                    </v-col>
                </v-row>
                <v-row class="mt-5">
                    <v-col cols="6">
                        <v-card-title class="font-weight-black  d-block vuetify-main-components"> Closing Date </v-card-title>
                    </v-col>
                    <v-col cols="3">
                        <v-btn
                            color="red"
                            text
                            dense
                            @click="clearResult"
                            large rounded  
                            class="float-right"
                            style="font-size:10px !important"     
                        >
                            Clear
                        </v-btn>
                    </v-col>
                    <v-col cols="3">
                        <v-btn
                            color="accent"
                            elevation="5"
                            dense
                            @click="searchResult"
                            large outlined raised rounded text
                            class="float-right"
                            style="font-size:10px !important"     
                        >
                            Search
                        </v-btn>
                    </v-col>
                </v-row>
            </v-container>
        </v-card>

    </div>
</template>


<script>
import DatePicker from '@/components/common/DatePicker'
import { mapGetters, mapActions } from 'vuex'

export default {
    components: {
        DatePicker,
    },
    data() {
        return {
            items: ["Ethiopia", "PPA", "Construction"],
            closing_date_start: '',
            closing_date_end: '',
            issued_date_start: '',
            issued_date_end: '',
            selected_values: [],
            selected_sectors:[],
            selected_companies:[],
            selected_types:[],
            selected_countries:[]
        };
    },
    computed: {
        ...mapGetters(["getTenderSector", "getTenderType", "getTenderCountries", "getCompanyNames", "getCompanyDict", "getDatePickerResetState"]),
        dateRangeText () {
            return this.dates.join(' ~ ')
        },
    },
    methods: {
        ...mapActions(["searchTenders", "setTenderData", "setTenderSearchParams", "setDatePickerResetState"]),
        updateIssuedDateStart(date){
            this.issued_date_start = date
        },
        updateIssuedDateEnd(date){
            this.issued_date_end = date
        },
        updateClosingDateStart(date){
            this.closing_date_start = date
        },
        updateClosingDateEnd(date){
            this.closing_date_end = date
        },
        changeCompanyValues(){ 
            let ids = []
            for (let name in this.selected_companies){
                if(this.getCompanyDict[ this.selected_companies[name] ])
                    ids.push( this.getCompanyDict[ this.selected_companies[name] ] );
            }
            return ids
        },
        searchResult(){
            let ids = this.changeCompanyValues();

            let searchQueries = {
                query: true,
                queries:{
                    size: 12,
                    company_id : ids.join(','),
                    name : this.selected_values.join(','),
                    type : this.selected_types.join(','),
                    sector : this.selected_sectors.join(','),    
                    closing_date_start: this.closing_date_start,
                    closing_date_end: this.closing_date_end,
                    issued_date_start: this.issued_date_start,
                    issued_date_end: this.issued_date_end,
                    country: this.selected_countries.join(',')
                }
            }
            this.setTenderData( [] )
            this.setTenderSearchParams( searchQueries )
            this.searchTenders( searchQueries )
        },
        clearResult(){
            this.closing_date_start
            let searchQueries = {
                query: true,
                queries:{
                    size: 12,
                }
            }
            
            this.selected_companies = [];
            this.selected_values = [];
            this.selected_types = [];
            this.selected_sectors = [];    
            this.closing_date_end = null;
            this.closing_date_start = null;
            this.issued_date_start = null;
            this.issued_date_end = null;
            this.selected_countries = [];
            this.setDatePickerResetState( !this.getDatePickerResetState );

            this.setTenderData( [] )
            this.setTenderSearchParams( searchQueries )
            this.searchTenders( searchQueries )
        }
    }
}
</script>

