        <v-layout row >
            <v-flex  class="mt-5" xs2 v-if="$vuetify.breakpoint.smAndDown">
                <SearchTab/>
            </v-flex>
            <v-flex sm2 xs4 class="ml-auto switch-data">
                <v-switch
                  label="Table view"
                  @change="changeViewStyle"
                  v-model="viewOption"
                ></v-switch>
            </v-flex>
        </v-layout>

        <v-layout row>
            <v-flex>
                <div v-if="getTab == 'Tender'">
                    <TenderTable/>
                </div>
                <div v-if="getTab == 'Award'">
                    <AwardTable/>
                </div>
                <div v-if="getTab == 'Company'">
                    <CompanyTable/>
                </div>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
import SearchTab from '@/components/Public/SearchTab'
import TenderTable from '@/components/Tender/TenderTable'
import CompanyTable from '@/components/Company/CompanyTable'
import AwardTable from '@/components/Award/AwardTable'
import { mapGetters, mapActions } from "vuex";

export default {
    components:{
      TenderTable,
      CompanyTable,
      AwardTable,
      SearchTab,
    },
    data(){
        return {
            currentPage: 1,
            viewOption: false,
        }
    },
    computed: {
        ...mapGetters(["getTab","getAwardData","getCompanyData","getTenderData","getViewOption"]),
    },
    methods: {
        ...mapActions(["changeViewOption"]),
        handlePageChange(value) {
            this.currentPage = value;
        },
        changeViewStyle() {
            this.changeViewOption(!this.getViewOption)
        }
    },
    created() {
        this.viewOption = this.getViewOption;
    }
}
</script>


